import uniGrid from '@/components/uni-grid/uni-grid.vue'
import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue'
import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue'
export default {
	components: {
		uniGrid,
		uniGridItem,
		uniNoticeBar
	},
	data() {
		return {
			text:"欢迎你来到天下菜系的APP。加油，奥利给！",
			list: [{
					url: '../../static/svg/地三鲜.png',
					text: '菜谱咨询',
					path:"./zixun/zixun"
				},
				{
					url: '../../static/svg/茶点.png',
					text: '菜谱建立'
				},
				{
					url: '../../static/svg/黑芝麻糊.png',
					text: '查询菜谱'
				},
				{
					url: '../../static/svg/海南鸡饭.png',
					text: '菜谱推荐'
				},
				{
					url: '../../static/svg/qifei.jpg',
					text: '起飞'
				},

			]
		}
	},
	methods: {
		change(e) {
			
			let {index} = e.detail
			let item = this.list[index];
			console.log(item)
			if(item.text=="起飞"){
				uni.showModal({
				    content: "您是否选择起飞！？ ",
				    success: function (res) {
				        if(res.confirm){
				            const bgAudioMannager = uni.getBackgroundAudioManager();
				            bgAudioMannager.title = '致爱丽丝';
				            bgAudioMannager.singer = '暂无';
				            bgAudioMannager.coverImgUrl = 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/audio/music.jpg';
				            bgAudioMannager.src = '../../static/mp3/VariousArtists.mp3';
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    },
				});
			}else{
				uni.navigateTo({
				    url:item.path
				});
			}
	
			
		},
		

	}
}
